*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body,
html{
    background-color: #fff;
    font-family: 'Noto Sans', sans-serif;
    overflow: hidden;
    background-color: #638fc2;
}

header{
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.3);
    position: sticky;
    top: 0;
    background-color: #fff;
}

nav{
    min-height: 13vh;
    margin: auto;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.svg-icon {
    width: 2em;
    height: 2em;
}
  
.svg-icon path,
.svg-icon polygon,
.svg-icon rect {
    fill: #4691f6;
}
  
.svg-icon circle {
    stroke: #4691f6;
    stroke-width: 1;
}

h1{
    margin: auto;
}

#astray{
    background-color: #fff;
    border: none;
    cursor: pointer;
}

section{
    height: 70vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.form{
    margin-top: 20%;
    margin-bottom: 20%;
    width: 300%;
    position: relative;
    right: 85%;
    height: 50px;
    overflow: hidden;
}

.form input{
    width: 100%;
    height: 100%;
    color: #b9b3ce;
    padding-top: 20px;
    border: none;
    outline: none;
    background-color: #638fc2;
}

.form label{
    position: absolute;
    bottom: 0px;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    border-bottom: 1px solid #000;
}

.form label::after{
    content: "";
    position: absolute;
    left: 0px;
    bottom: -1px;
    height: 100%;
    width: 100%;
    border-bottom: 3px solid #b9b3ce;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
}

.content-name{
    position: absolute;
    bottom: 5px;
    left: 0px;
    transition: all 0.3s ease;
}

 .form input:focus + .label-name .content-name,
 .form input:valid + .label-name .content-name{
    transform: translateY(-150%);
    font-size: 14px;
 }

 .form input:focus + .label-name::after,
 .form input:valid + .label-name::after{
    transform: translateX(0%);
 }

 h2{
    margin-top: 30%;
 }

 #GetInfo{
    position: absolute;
    cursor: pointer;
    padding: 30px;
    background-color: #638fc2;
    border: none;
    font-size: 20px;
    left: 42.5%;
 }